<template>
	<div class="bg">
		<!-- <img class="bgImg" src="../images/resign_bg.png" alt=""> -->
		<div class="download">
			<img src="../images/address_logo.png">
			<b>下载“急借号”获取每周新口子 POS机免费送</b>
			<span @click="openApp">立即下载</span>
		</div>
		<div class="phone">
			<input type="number" placeholder="请输入您的手机号码" v-model="phone" oninput="if(value.length>11)value=value.slice(0,11)">
		</div>
		<div class="smsCode">
			<input maxlength="4" type="text" placeholder="请输入验证码" v-model="smsCode">
			<span @click="send">
				<timer-btn ref="btn" :second="60"></timer-btn>
			</span>
			<div v-show='boxType' class="forgetBox" :state='boxState' @click='box'></div>
		</div>
		<div class="password">
			<input id="password" type="password" placeholder="请设置6-20位登录密码" @focus="focus" v-model="password">
			<div v-show='yanjing'>
				<img v-if='eyeShow == 1' src="../images/yanjing.png" @click='eyeDisplay'/>
				<img v-if='eyeShow == 2' src="../images/yincang.png" @click='eyeHide'/>
			</div>
		</div>
		<div class="register" @click="login">
			<!-- <img src="../images/res_btn.png" alt=""> -->
		</div>
		<div class="bottomText">
			<div class="left">
				<span class="checkbox" @click="pols">
					<img src="../images/checkActive.png" v-show="pol">
				</span>
				<label>同意《使用协议》</label>
			</div>
			<div class="right" @click="into">已注册，立即登录</div>
		</div>
	</div>
</template>
<script>
let Base64 = require('js-base64').Base64
import { strEnc } from '../common/js/des.js'
import { Toast } from 'mint-ui'
import { fetchSmsVCode, verifySms, loginRegister } from './../api/api'
import { mapMutations } from 'vuex'
export default {
	data() {
		return {
			phone: '', // 手机电话
			boxType: true, // 短信验证码状态
			smsCode: '', // 验证码
			yanjing: false, // 眼睛显示隐藏
			eyeShow: 1, // 密码显示隐藏
			password: '', // 密码
			invitationCode: '', // 邀请码
			client: 'wx', // 客户端类型
			registerAddr: '', // 注册地址
			channelCode: '', // 渠道编码
			registerCoordinate: '', // 注册坐标，经纬度
			userInfo: '', // 用户信息
			pol: true
		}
	},
	methods: {
		...mapMutations([
			'RECORD_USERINFO', 'GET_USERINFO'
		]),
		into() {
			this.$router.push({path: '/login'})
		},
		openApp() {
			var u = navigator.userAgent
            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1
            var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
            if (isAndroid === true) {
            	window.location = 'http://sj.qq.com/myapp/detail.htm?apkName=com.minhua.jijiehao2'
            } else if (isiOS === true) {
            	window.location = 'https://itunes.apple.com/cn/app/id1324980243?mt=8'
            }
		},
		pols() {
			if (this.pol === true) {
				this.pol = false
			} else {
				this.pol = true
			}
		},
		box() {
			var phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
			if (phoneReg.test(this.phone)) {
    		} else {
    			Toast({
					message: '请输入正确手机号码',
					position: 'bottom',
					duration: 2000
				});
    		}
		},
		focus() {
    		this.yanjing = true
    	},
		send() {
			var phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
			if (phoneReg.test(this.phone)) {
				let params = {
					phone: this.phone,
					type: 'REGISTER'
				}
				fetchSmsVCode(params).then(res => {
					if (res.data.state === '10') {
						Toast({
							message: res.msg,
							position: 'bottom',
							duration: 2000
						});
					} else if (res.data.state === '20') {
						Toast({
							message: res.data.message,
							position: 'bottom',
							duration: 2000
						});
					} else if (res.code === 500) {
						Toast({
							message: res.msg,
							position: 'bottom',
							duration: 2000
						});
					}
				})
			} else {
				Toast({
					message: '请输入正确手机号码',
					position: 'bottom',
					duration: 2000
				});
			}
		},
		eyeDisplay() {
			var psd = document.getElementById('password')
    		this.eyeShow = 2
    		psd.type = 'text'
		},
		eyeHide() {
			var psd = document.getElementById('password')
    		this.eyeShow = 1
    		psd.type = 'password'
		},
		login() {
			var phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
			var passReg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/
			if (phoneReg.test(this.phone)) {
				if (this.pol === true) {
					if (this.smsCode === '' || this.smsCode.length !== 4) {
						Toast({
							message: '请输入正确的验证码',
							position: 'bottom',
							duration: 2000
						});
					} else {
						let params = {
							phone: this.phone,
							type: 'REGISTER',
							vcode: this.smsCode
						}
						verifySms(params).then(res => {
							if (res.data.state === '10') {
								var key = 'pasec23der1e12ecjer3AFRGYD03847@@*^DEJUsdu33'
								var aa = strEnc(this.password, key)
								var loginWord = Base64.encode(aa)
								if (passReg.test(this.password)) {
									let params = {
										loginName: this.phone,
										loginPwd: loginWord,
										vcode: this.smsCode,
										invitationCode: this.invitationCode,
										client: this.client,
										registerAddr: this.registerAddr,
										channelCode: this.channelCode,
										registerCoordinate: this.registerCoordinate,
										signMsg: '',
										blackBox: '',
										mobileType: '3'
									}
									loginRegister(params).then(res => {
										if (res.code === 200) {
											this.userInfo = res.data
											if (!this.userInfo.userId) {
												alert('登录失败')
											} else {
												this.RECORD_USERINFO(this.userInfo)
												Toast({
													message: res.msg,
													position: 'bottom',
													duration: 2000
												});
												this.$router.push({path: '/index'})
											}
											// var u = navigator.userAgent
								   //          var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1
								   //          var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
								   //          if (isAndroid === true) {
								   //          	window.location = 'http://sj.qq.com/myapp/detail.htm?apkName=com.minhua.jijiehao2'
								   //          } else if (isiOS === true) {
								   //          	window.location = 'https://itunes.apple.com/cn/app/id1324980243?mt=8'
								   //          }
										} else if (res.code !== 200) {
											Toast({
												message: res.msg,
												position: 'bottom',
												duration: 2000
											});
										}
									})
								} else {
									Toast({
										message: '密码为6-16位字符，包括数字和字母',
										position: 'bottom',
										duration: 2000
									});
								}
							} else if (res.data.state === '20') {
								Toast({
									message: res.data.message,
									position: 'bottom',
									duration: 2000
								});
							}
						})
					}
				} else {
					Toast({
						message: '请同意使用协议',
						position: 'bottom',
						duration: 2000
					})
				}
			} else {
				Toast({
					message: '请输入正确手机号码',
					position: 'bottom',
					duration: 2000
				})
			}
		}
	},
	computed: {
		boxState() {
			var phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
  			if (phoneReg.test(this.phone)) {
  				this.boxType = false
  			} else {
  				this.boxType = true
  			}			
  		}
	},
	mounted() {
  		this.channelCode = this.$route.query.channelCode
  		this.invitationCode = this.$route.query.invitationCode
	},
	components: {
		'timer-btn': require('../components/PlugInUnit/timerbtn.vue')
	}
}
</script>
<style lang="scss" rel="stylesheet/scss">
	.bg {
		z-index: 11;
		position: relative;
		background: url(../images/resign_bg.png) no-repeat center;
		width: 100%;
		height: 100%;
		background-size: 100% 100%;
		overflow: auto;
		.download {
			width: 100%;
			height: 0.8rem;
			position: absolute;
			top: 0;
			left: 0;
			background: rgba(0,0,0,0.7);
			box-sizing: border-box;
			img {
				vertical-align: middle;
				border-radius: 0.1rem;
			}
			img:nth-child(1) {
				width: 0.5rem;
				position: absolute;
				top: 50%;
				left: 0.22rem;
				transform: translateY(-50%);
			}
			b {
				color: #fff;
				font-size: 0.22rem;
				position: absolute;
				top: 50%;
				left: 0.92rem;
				transform: translateY(-50%);
			}
			span {
				position: absolute;
				color: #fff;
				right: 0.22rem;
				top: 50%;
				transform: translateY(-50%);
				font-size: 0.22rem;
				padding: 0.1rem 0.15rem;
				background-color: rgb(255,101,51);
				border-radius: 0.2rem;
			}
		}
		.bgImg {
			width: 100%;
			vertical-align: bottom;
		}
		input::-webkit-input-placeholder {
			color: #999;
		}
		input::-moz-input-placeholder {
			color: #999;
		}
		input::-ms-input-placeholder {
			color: #999;
		}
		.phone {
			// position: absolute;
			// bottom: 4.4rem;
			margin: 6.4rem auto 0;
			width: 90%;
			// left: 50%;
			// transform: translateX(-50%);
			// -webkit-transform: translateX(-50%);
			// -moz-transform: translateX(-50%);
			// -ms-transform: translateX(-50%);
			input {
				width: 100%;
				padding: 0.3rem 0.6rem;
				box-sizing: border-box;
				border-radius: 0.5rem;
				outline: none;
				font-size: 0.3rem;
			}
		}
		.smsCode {
			// position: absolute;
			// bottom: 3.2rem;
			width: 90%;
			margin: 0.3rem auto 0;
			position: relative;
			// left: 50%;
			// transform: translateX(-50%);
			// -webkit-transform: translateX(-50%);
			// -moz-transform: translateX(-50%);
			// -ms-transform: translateX(-50%);
			input {
				width: 100%;
				padding: 0.3rem 0.6rem;
				box-sizing: border-box;
				border-radius: 0.5rem;
				outline: none;
				font-size: 0.3rem;
			}
			button {
				position: absolute;
				top: 50%;
				right: 0.5rem;
				border: 0;
				-webkit-transform: translateY(-50%);
				-moz-transform: translateY(-50%);
				-ms-transform: translateY(-50%);
				background: transparent;
				font-size: 0.2rem;
				color: #ffca99;
			}
			.forgetBox {
				position: absolute;
				top: 0;
				right: 0.5rem;
				bottom: 0;
				width: 1.8rem;
			}
		}
		.password {
			// position: absolute;
			// bottom: 2rem;
			width: 90%;
			margin: 0.3rem auto 0;
			// left: 50%;
			// transform: translateX(-50%);
			// -webkit-transform: translateX(-50%);
			// -moz-transform: translateX(-50%);
			// -ms-transform: translateX(-50%);
			position: relative;
			input {
				width: 100%;
				padding: 0.3rem 0.6rem;
				box-sizing: border-box;
				border-radius: 0.5rem;
				outline: none;
				font-size: 0.3rem;
			}
			img {
				position: absolute;
				top: 50%;
				right: 0.5rem;
				width: 0.25rem;
				-webkit-transform: translateY(-50%);
				-moz-transform: translateY(-50%);
				-ms-transform: translateY(-50%);
			}
		}
		.register {
			// position: absolute;
			// bottom: 0.6rem;
			width: 90%;
			height: 1.12rem;
			margin: 0.3rem auto 0;
			// height: 1.12rem;
			border-radius: 0.5rem;
			// left: 50%;
			background: url(./../images/res_btn.png) no-repeat center;
			background-size: 100% 100%;
			// transform: translateX(-50%);
			// -webkit-transform: translateX(-50%);
			// -moz-transform: translateX(-50%);
			// -ms-transform: translateX(-50%);
			// img {
			// 	width: 100%;
			// }
		}
		.bottomText {
			// position: absolute;
			// bottom: 0.1rem;
			width: 90%;
			margin: 0 auto;
			margin-top: 0.3rem;
			// left: 50%;
			// transform: translateX(-50%);
			// -webkit-transform: translateX(-50%);
			// -moz-transform: translateX(-50%);
			// -ms-transform: translateX(-50%);
			font-size: 0.22rem;
			color: #fff;
			overflow: hidden;
			.right {
				float: right;
			}
			.left {
				float: left;
				.checkbox {
					width: 0.2rem;
					height: 0.2rem;
					background: url(./../images/checkbox.png) no-repeat center;
					background-size: cover;
					position: relative;
					display: inline-block;
					vertical-align: bottom;
					img {
						width: 0.23rem;
						position: absolute;
						left: 0;
						bottom: 0;
					}
				}
			}
			label {
				display: inline;
			}
		}
	}
</style>



